main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary-color: red;
  --secondary-color: black;
  --tertiary-color: #f4f4fa;
  --fourth-color: #f3a847;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--dark);
  color: var(--light);
  font-family: 'Poiret One', cursive;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--dark);
}



button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

span[role='img'] {
  cursor: default;
}

form {
  width: 100%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  cursor: pointer;
}

textarea {
  width: 100%;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

.navActive {
  color: var(--fourth-color);
}

.leftImg {
  border-radius: 50%;
  height: 7vw;
  min-height: 90px;
  padding: 0px 35px;
}

.leftName {
  text-align: center;
  padding-left: 10px;
  font-weight: bold;
  margin: 0;
  font-size: calc(2.5vw);
}

.leftHeader {
  display: flex;
  justify-content: right;
  align-items: center;
}

.navAlign{
  display: flex;
  text-align: center;
  align-items: center;
}

.onHover:hover{
  color: var(--fourth-color);
}

/* Work Styles Start */

.work {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  color: var(--tertiary-color);
  background: var(--secondary-color);
}

.work h3 {
  font-size: 3vw;
  border-right: 2px solid var(--primary-color);
  padding-right: 20px;
  width: 13vw;
  text-align: right;
}

.work .right {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.work .right .break {
  flex-basis: 100%;
  height: 0;
}

.work .right a {
  border: thick solid var(--primary-color);
  width: 100%;
}

.work .right .main {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 20vw;
  background-size: cover;
  background-position: left top;
  align-items: flex-end;
  margin-top: 10px;
}

.Social {
  background-image: url('./assets/Images/socialmediaAPIDark.png');
}

.Social:hover {
  background-image: url('./assets/Images/socialmediaAPI.png');
}

.Adventure {
  background-image: url('./assets/Images/Adventure-Screenshot-Dark.png');
}

.Adventure:hover {
  background-image: url('./assets/Images/Adventure-Screenshot.png');
}

.work .right .secondary {
  display: flex;
  margin-top: 10px;
  width: 49%;
  height: 125px;
  flex-direction: row;
  align-items: flex-end;
  
}

.work .right .horiseon {
  background-image: url('./assets/Images/horsieon\ dark.png');
  background-size: cover;
  background-position: left top;
}

.work .right .horiseon:hover {
  background-image: url('./assets/Images/horsieon.png');
}

.work .right .robot {
  background-image: url('./assets/Images/robot-gladiators\ dark.png');
  background-size: cover;
  background-position: left top;
}

.work .right .robot:hover {
  background-image: url('./assets/Images/robot-gladiators.png');
}

.work .right .portfolio {
  background-image: url('./assets/Images/Read-My-Lips-screenshot\ dark.png');
  background-size: cover;
  background-position: left top;
}

.work .right .portfolio:hover {
  background-image: url('./assets/Images/Read-My-Lips-screenshot.png');
}

.work .right .streamguard {
  background-image: url('./assets/Images/StreamGuard\ dark.png');
  background-size: cover;
  background-position: left top;
}

.work .right .streamguard:hover {
  background-image: url('./assets/Images/StreamGuard.png');
}

.work .right a div {
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 0, 0, 1);
  height: 70px;
  width: 100%;
  margin-bottom: 10px;
  padding-right: 10px;
  padding-left: 10px;
  justify-content: center;
}

.work .right h4 {
  font-size: 1.55vw;
  text-decoration: none;
  color: var(--tertiary-color);
}

.work p {
  color: var(--tertiary-color);
  font-size: 1.25vw;
}
/* Work Styles End */

.resumeCenter{
  justify-content: center;
  text-align: center;
  align-items: center;
}

.resumeCenter h4{
  align-self: flex-start;
}

.resumePara{
  justify-content:center;
  width: 100%;
  
  
}